import { PLATFORM_NAME, weChatPlatform, uniPlatform, aliPayPlatform, baiduPlatform, byteDancePlatform, dingDingPlatform, } from "./platform";
//是否存在key记录
function hasKey(platform, key) {
    var info = platform.getStorageInfoSync();
    return !!info.keys.includes(key);
}
//方法处理
function storageMethods(opts) {
    var platform = opts.platform, platformName = opts.platformName;
    //选项式风格 支付宝和百度为选项式风格
    var optsStyle = [
        PLATFORM_NAME.ALIPAY,
        PLATFORM_NAME.BAIDU,
        PLATFORM_NAME.DINGDING,
    ].includes(platformName);
    var setStorageSync = platform.setStorageSync, getStorageSync = platform.getStorageSync, removeStorageSync = platform.removeStorageSync;
    return {
        //设置存储
        setStorage: (function () {
            return function (opts) {
                var key = opts.key, data = opts.data;
                if (optsStyle) {
                    setStorageSync({
                        key: key,
                        data: data,
                    });
                }
                else {
                    setStorageSync(key, data);
                }
            };
        })(),
        //获取存储
        getStorage: (function () {
            return function (opts) {
                var key = opts.key;
                if (optsStyle) {
                    //支付宝，百度存在data包围
                    var result = getStorageSync({
                        key: key,
                    });
                    return result.data;
                }
                else {
                    return getStorageSync(key);
                }
            };
        })(),
        //删除数据
        removeStorage: (function () {
            return function (opts) {
                var key = opts.key;
                if (optsStyle) {
                    return removeStorageSync({
                        key: key,
                    });
                }
                else {
                    return removeStorageSync(key);
                }
            };
        })(),
        //是否存在key值
        hasKey: function (key) {
            //@ts-ignore
            return hasKey(platform, key);
        },
    };
}
//浏览器
function browser() {
    var localStorage = window.localStorage;
    //可存储的数据类型
    var dataTypes = ["object", "string", "number", "boolean", "undefined"];
    return {
        setStorage: function (opts) {
            var key = opts.key, data = opts.data;
            var type = typeof data;
            //写入的值
            var value = (function () {
                //属于正常数据类型
                if (dataTypes.includes(type)) {
                    return {
                        type: type,
                        data: data,
                    };
                }
                else {
                    return {
                        type: type,
                    };
                }
            })();
            //写入解析好的数据
            localStorage.setItem(key, JSON.stringify(value));
        },
        getStorage: function (opts) {
            var key = opts.key;
            try {
                var storageValue = JSON.parse(localStorage.getItem(key));
                var data = storageValue.data, type = storageValue.type;
                //undefined提供会undefined处理
                if (type === "undefined")
                    return "";
                //如果存在值
                if (data !== undefined) {
                    return data;
                }
                return data.toString();
            }
            catch (e) {
                return localStorage.getItem(key);
            }
        },
        removeStorage: function (opts) {
            var key = opts.key;
            return localStorage.removeItem(key);
        },
        //是否存在key值
        hasKey: function (key) {
            return key in localStorage;
        },
    };
}
//兼容处理
var storage = (function () {
    if (uniPlatform()) {
        //uni-app
        return storageMethods({
            //@ts-ignore
            platform: uni,
            platformName: PLATFORM_NAME.UNI,
        });
    }
    else if (aliPayPlatform()) {
        //支付宝小程序
        return storageMethods({
            //@ts-ignore
            platform: my,
            platformName: PLATFORM_NAME.ALIPAY,
        });
    }
    else if (dingDingPlatform()) {
        //钉钉
        return storageMethods({
            //@ts-ignore
            platform: dd,
            platformName: PLATFORM_NAME.DINGDING,
        });
    }
    else if (weChatPlatform()) {
        //微信小程序
        return storageMethods({
            //@ts-ignore
            platform: wx,
            platformName: PLATFORM_NAME.WECHAT,
        });
    }
    else if (baiduPlatform()) {
        //微信小程序
        return storageMethods({
            //@ts-ignore
            platform: swan,
            platformName: PLATFORM_NAME.BAIDU,
        });
    }
    else if (byteDancePlatform()) {
        //字节跳动
        return storageMethods({
            //@ts-ignore
            platform: tt,
            platformName: PLATFORM_NAME.BYTEDANCE,
        });
    }
    else if (window && window.localStorage) {
        //浏览器
        return browser();
    }
    console.warn("\u5F53\u524D\u73AF\u5883\u4E0D\u652F\u6301");
    return null;
})();
export default storage;
